.enemiesFields {
  width: 35%;
  margin-left: 3%;
}

.enemiesFieldsTitle {
  text-align: center;
}

.fieldCell {
  border-bottom: 1px solid #000;
}

.fieldCell:nth-child(2) {
  border-left: 1px solid #000;
  border-right: 1px solid #000;
}

.field {
  border: 0;
  line-height: 18px;
  padding: 4px;
  width: 100%;
}
